@import '../style-imports';

$paddingTop: 25px;
$paddingBottom: 55px;

/* align footer content and footer logo section in flex */
.footer {
  display:flex;
  width: 100%;

  padding: $paddingTop 0 $paddingBottom 0;

  color: $textColorMedium;
  font-weight: bold;

  /* footer content can wrap */
  .footerContent {
    display: flex;
    flex-grow: 1;
    flex-wrap: wrap;
  }

  h3 {
    font-size: 1.5rem;
    color: $colorY;
  }
  section {
    flex: 1 1 0; /* equal width sections */
    padding-right: 100px;
  }

  .footerLogo {
    flex-grow: 0;
    flex-shrink: 0;

    /* add additional padding to compensate for
       h3 padding
    */
    padding-top: $paddingTop;

    /* move image and copyright text to the right */
    direction: rtl;

    img {
      width: 75px;
    }
  }
}

