/*
  remove active outline from buttons
  https://stackoverflow.com/questions/23333231/bootstrap-button-shows-blue-outline-when-clicked
*/
button:focus, button:active {
  outline: none; }

/*
  container to prevent button from jumping
  on edge case
  when it's translating itself out of the mouse position
*/
.buttonContainer {
  position: relative;
  display: inline-block;
  cursor: pointer;
  z-index: 0;
  /* to allow z-index: -1 on shadow */ }

/*
  translate on hover
*/
.buttonContainer:hover button {
  transform: translate(4px, 3px); }

.button {
  cursor: pointer;
  font-size: 1.4rem;
  padding: 0.5rem 1.5rem;
  border: 2px solid #000;
  color: #fff;
  background-color: #000; }

/*
  striped shadow
*/
.buttonContainer:after {
  position: absolute;
  display: block;
  content: "";
  top: 7px;
  left: 8px;
  width: 100%;
  height: 100%;
  /*
    so shadow doesn't grow larger with border
    than button without border
  */
  box-sizing: border-box;
  border: 2px solid #000;
  /*
    stripes
    https://css-tricks.com/stripes-css/
  */
  background: repeating-linear-gradient(45deg, transparent, transparent 5px, #000 5px, #000 7px);
  z-index: -1;
  /* move behind button */ }

/*
  MODIFIERS
*/
/* inverted */
.inverted .button {
  color: #000;
  background-color: #fff; }

.inverted:after {
  background: repeating-linear-gradient(45deg, #fff, #fff 5px, #000 5px, #000 7px); }

/* textYellow */
.yellowText {
  color: #ffe600; }
  .yellowText .button {
    color: #ffe600; }
