@import '../style-imports';

$paddingBreakOut: 0px;
$paddingWide: 40px;
$paddingDefault: 80px;
$paddingNarrow: 140px;

.contentContainer {
  min-height: 25px;
  overflow: hidden;
  max-width: 100%;
  box-sizing: border-box;

  background-color: $backgroundColorLight;
}

/* MODIFIERS WIDTHX */
.breakOut {
  padding: 0 $paddingBreakOut 0 $paddingBreakOut;
}
.wide {
  padding: 0 $paddingWide 0 $paddingWide;
}
.default {
  padding: 0 $paddingDefault 0 $paddingDefault;
}
.narrow {
  padding: 0 $paddingNarrow 0 $paddingNarrow;
}

/* MODIFIERS BACKDROP */
.light {
  background-color: $backgroundColorLight;
}
.medium {
  background-color: $backgroundColorMedium;
}
.dark {
  background-color: $backgroundColorDark;
}
