/* align footer content and footer logo section in flex */
.footer {
  display: flex;
  width: 100%;
  padding: 25px 0 55px 0;
  color: #808080;
  font-weight: bold;
  /* footer content can wrap */ }
  .footer .footerContent {
    display: flex;
    flex-grow: 1;
    flex-wrap: wrap; }
  .footer h3 {
    font-size: 1.5rem;
    color: #ffe600; }
  .footer section {
    flex: 1 1 0;
    /* equal width sections */
    padding-right: 100px; }
  .footer .footerLogo {
    flex-grow: 0;
    flex-shrink: 0;
    /* add additional padding to compensate for
       h3 padding
    */
    padding-top: 25px;
    /* move image and copyright text to the right */
    direction: rtl; }
    .footer .footerLogo img {
      width: 75px; }
